.lightbox {
  width: 100%;
  height: 100%;
  position: fixed;
  padding: 1.25rem;
  justify-content: center;
  align-items: center;
  display: none;
  top: 0;
  left: 0;
  background: hsla(0deg, 0%, 0%, 0.7);
  z-index: 99;
}
.lightbox.lightbox-visible {
  display: flex;
}
.lightbox .lightbox__content {
  width: 100%;
  max-width: 31.25rem;
  margin: auto;
  position: relative;
  padding: 1.25rem;
}
@media (min-width: 71.875em) {
  .lightbox .lightbox__content {
    width: 30%;
    min-width: 31.25rem;
  }
}
.lightbox .lightbox__content .lightbox__content-main {
  position: relative;
  text-align: right;
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-main-close {
  position: relative;
  display: inline-block;
  text-align: right;
  padding: 0.625rem;
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-main-close:hover #lightbox-close path {
  fill: hsl(26deg, 100%, 55%);
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-main-close #lightbox-close path {
  fill: hsl(0deg, 0%, 100%);
}
.lightbox .lightbox__content .lightbox__content-main ul li {
  display: none;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.lightbox .lightbox__content .lightbox__content-main ul li.active {
  display: block;
  opacity: 1;
}
.lightbox .lightbox__content .lightbox__content-main img {
  width: 100%;
  border-radius: 0.9375rem;
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-nav {
  position: absolute;
  top: 50%;
  width: 100%;
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-nav .nav-prev,
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-nav .nav-next {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: hsl(0deg, 0%, 100%);
  background-repeat: no-repeat;
  background-position: center;
  user-select: none;
  cursor: pointer;
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-nav .nav-prev {
  left: -1.25rem;
  background-image: url("../images/icon-previous.svg");
}
.lightbox .lightbox__content .lightbox__content-main .lightbox__content-nav .nav-next {
  right: -1.25rem;
  background-image: url("../images/icon-next.svg");
}
.lightbox .lightbox__content .lightbox__content-thumbs {
  padding-top: 1.25rem;
}
.lightbox .lightbox__content .lightbox__content-thumbs ul {
  display: flex;
}
.lightbox .lightbox__content .lightbox__content-thumbs ul li {
  position: relative;
  padding: 0.625rem;
}
.lightbox .lightbox__content .lightbox__content-thumbs ul li a {
  border: 0.125rem solid transparent;
}
.lightbox .lightbox__content .lightbox__content-thumbs ul li.selected a {
  display: flex;
  overflow: hidden;
  position: relative;
  border-radius: 0.9375rem;
  border-color: hsl(26deg, 100%, 55%);
}
.lightbox .lightbox__content .lightbox__content-thumbs ul li.selected a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: hsla(0deg, 0%, 100%, 0.7);
}
.lightbox .lightbox__content .lightbox__content-thumbs img {
  width: 100%;
  border-radius: 0.9375rem;
}